html, body ,#root, .App, main {
  height: 100%;
}

@font-face {font-family: "iconfont";
  src: url('//at.alicdn.com/t/font_440195_fw8hsnrso53wstt9.eot?t=1507880439470'); /* IE9*/
  src: url('//at.alicdn.com/t/font_440195_fw8hsnrso53wstt9.eot?t=1507880439470#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
  url('//at.alicdn.com/t/font_440195_fw8hsnrso53wstt9.ttf?t=1507880439470') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
  url('//at.alicdn.com/t/font_440195_fw8hsnrso53wstt9.svg?t=1507880439470#iconfont') format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-loading:before { content: "\e601"; }

.icon-search:before { content: "\e63f"; }

.icon-paihang:before { content: "\e68d"; }

.icon-beimei:before { content: "\e787"; }

section {
  display: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: calc(100% - 45px);
  overflow: scroll;
  padding-left: 10px;
  padding-right: 10px;
}

section.active {
  display: block;
}


